const initialState = {
  allData: [],
  data: [],
  total: 1,
  params: {},
  selected: null,
};

const terms = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_TERM_PRODUCT":
      return {
        ...state,
        data: action.data?.data,
        total: action.totalPages,
        params: action.params,
        err: null,
        status: null,
      };
    case "GET_TERM_PRODUCT":
      return { ...state, selected: action.selected };
    case "GET_PRODUCT":
      return { ...state, productDetail: action.selected, status: null };
    case "ADD_TERM_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_TERM_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "DELETE_TERM_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };
    default:
      return { ...state };
  }
};
export default terms;
