import api from "../../../constants/api";

import Service from "./../../../services/request";

export const getListCountry = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_LIST_COUNTRY.method,
      path: api.GET_LIST_COUNTRY.path,
    }).then((response) => {
      dispatch({
        type: "LIST_COUNTRY",
        data: response?.data?.data,
        clear: params?.clear,
      });
    });
  };
};
export const getListCityByCountry = (country_id) => {
  console.log(country_id);
  return async (dispatch) => {
    await Service.send({
      method: api.GET_LIST_CITY.method,
      path: api.GET_LIST_CITY.path,
      query: { show: "all" },
      data: {
        filter: {
          country_id: country_id.countryId || 0,
        },
        skip: 0,
        limit: undefined,
        order: {
          key: "id",
          value: "DESC",
        },
      },
    }).then((response) => {
      dispatch({
        type: "LIST_CITY",
        data: response?.data?.data,
      });
    });
  };
};
export const getLanguage = (data) => {
  return async (dispatch) => {
    dispatch({
      type: "GET_LANGUAGE",
      data,
    });
  };
};
