const initialState = {
  allData: [],
  data: [],
  dataExport: [],
  total: 1,
  params: {},
  selected: null,
  timeTableDetail: {},
  type: null,
};

const timeTable = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_TIMETABLE":
      return {
        ...state,
        data: action?.data || [],
        status: null,
        selected: null,
      };

    case "GET_DATA_EXPORT_TIMETABLE":
      return {
        ...state,
        dataExport: action?.data || [],
      };

    case "ADD_TIMETABLE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };

    case "GET_TIMETABLE_DETAIL":
      return {
        ...state,
        selected: action?.selected,
      };
    case "DELETE_TIMETABLE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };
    case "UPDATE_TIMETABLE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };

    case "GET_PRODUCT_ID":
      return { ...state, productDetail: action.selected };
    case "GET_TERM_ID":
      return { ...state, sectionDetail: action.selected };
    default:
      return { ...state };
  }
};
export default timeTable;
