const initialState = {
  allData: [],
  data: [],
  dataExport: [],
  total: 1,
  params: {},
  selected: null,
  productDetail: {},
  product: [],
};

const course_document = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_DOCUMENT":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_DATA_EXPORT_DOCUMENT":
      return {
        ...state,
        dataExport: action.data,
      };
    case "GET_DOCUMENT_COURSE":
      return { ...state, selected: action.selected };
    case "GET_PRODUCT_DETAIL":
      return { ...state, productDetail: action.selected };
    case "ADD_DOCUMENT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "GET_DATA_PRODUCT":
      return {
        ...state,
        product: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "DELETE_COURSE_DOCUMENT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };
    case "UPDATE_DOCUMENT_COURSE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    default:
      return { ...state };
  }
};
export default course_document;
