const initialState = {
  allData: [],
  data: [],
  total: 1,
  params: {},
  selected: null,
  categoryDetail: {},
};

const categoryPost = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_CATEGORY":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_CATEGORY":
      return { ...state, selected: action.selected };
    case "ADD_CATEGORY":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_CATEGORY":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    default:
      return { ...state };
  }
};
export default categoryPost;
