const initialState = {
  allData: [],
  data: [],
  total: 1,
  category: [],
  categoryProduct: [],
  certifycate: [],
  params: {},
  selected: null,
  categoryDetail: {},
  categoryProductDetail: {},
  certificateDetail: {},
  instractors: [],
};

const products = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_PRODUCT":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
        err: null,
        selected: null,
      };
    case "GET_PRODUCT":
      return { ...state, selected: action.selected };
    case "ADD_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "DELETE_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };
    case "GET_DATA_CATEGORY":
      return {
        ...state,
        category: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_DATA_INSTRACTORS":
      return {
        ...state,
        instractors: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_DATA_CERTIFYCATE":
      return {
        ...state,
        certifycate: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_CATEGORY":
      return { ...state, categoryDetail: action.selected };
    case "GET_CERTIFICATEID":
      return { ...state, certificateDetail: action.selected };
    case "UPDATE_CATEGORY":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };

    //cate product
    case "GET_DATA_CATEGORY_PRODUCT":
      return {
        ...state,
        categoryProduct: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_CATEGORY_PRODUCT":
      return { ...state, categoryProductDetail: action.selected };
    case "UPDATE_CATEGORY_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };

    default:
      return { ...state };
  }
};
export default products;
