const initialState = {
  allData: [],
  data: [],
  dataExport: [],
  total: 1,
  params: {},
  selected: null,
  sectionDetail: {},
  section: [],
};

const course_document = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_QUIZ":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
        selected: null,
        sectionDetail: {},
      };
    case "GET_DATA_EXPORT_QUIZ":
      return {
        ...state,
        dataExport: action.data,
      };

    case "GET_QUIZ":
      return { ...state, selected: action.selected };
    case "GET_section_DETAIL":
      return { ...state, sectionDetail: action.selected };
    case "ADD_QUIZ":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "GET_DATA_SECTION":
      return {
        ...state,
        section: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "UPDATE_QUIZ":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    default:
      return { ...state };
  }
};
export default course_document;
