import { useHistory } from "react-router-dom";
// ** Initial State
const initialState = {
  allData: [],
  data: [],
  total: 1,
  category: [],
  params: {},
  selected: null,
  categoryDetail: {},
};

const documents = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_DOCUMENT":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
        err: null,
      };
    case "GET_DOCUMENT":
      return { ...state, selected: action.selected.data };
    case "GET_CATEGORY":
      return { ...state, categoryDetail: action.selected };
    case "ADD_DOCUMENT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_DOCUMENT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "GET_DATA_CATEGORY":
      return {
        ...state,
        category: action.data,
        totals: action.totalPages,
        params: action.params,
        status: null,
      };
    case "DELETE_DOCUMENT":
      return { ...state };
    default:
      return { ...state };
  }
};
export default documents;
