const common = (state = {}, action) => {
  switch (action.type) {
    case "LIST_COUNTRY":
      return { ...state, ListCountry: action.data };

    case "LIST_CITY":
      return { ...state, ListCity: action.data };
    case "GET_LANGUAGE":
      return { ...state, language: action.data };

    default:
      return state;
  }
};

export default common;
