const initialState = {
  allData: [],
  data: [],
  total: 1,
  params: {},
  selected: null,
};

const prices = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_PRICE_PRODUCT":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
        err: null,
        selected: null,
      };
    case "GET_PRICE_PRODUCT":
      return { ...state, selected: action.selected };
    case "ADD_PRICE_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_PRICE_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "DELETE_PRICE_PRODUCT":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
      };
    default:
      return { ...state };
  }
};
export default prices;
