const initialState = {
  allData: [],
  data: [],
  dataExport: [],
  total: 1,
  params: {},
  selected: null,
  productDetail: null,
};

const certificateUser = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_USER_CERTIFICATE":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
      };
    case "GET_DATA_EXPORT_USER_CERTIFICATE":
      return {
        ...state,
        dataExport: action.data,
      };
    case "GET_PRODUCT":
      return { ...state, productDetail: action.selected };
    case "RESET_DATA_USER_CERTIFICATE":
      return {
        ...state,
        allData: [],
        data: [],
        total: 1,
        params: {},
        status: null,
        selected: null,
      };

    case "GET_USER_CERTIFICATE":
      return { ...state, selected: action.selected };
    case "ADD_USER_CERTIFICATE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_USER_CERTIFICATE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    default:
      return { ...state };
  }
};
export default certificateUser;
