const initialState = {
  allData: [],
  data: [],
  dataExport: [],
  total: 1,
  params: {},
  selected: null,
  product: [],
};

const certificate = (state = initialState, action) => {
  switch (action.type) {
    case "GET_DATA_CERTIFICATE":
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params,
        status: null,
        selected: null,
      };
    case "GET_DATA_EXPORT_CERTIFICATE":
      return {
        ...state,
        dataExport: action.data,
      };
    case "GET_DATA_PRODUCT":
      return {
        ...state,
        product: action.data,
        status: null,
        err: null,
      };
    case "GET_CERTIFICATE":
      return { ...state, selected: action.selected };
    case "ADD_CERTIFICATE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    case "UPDATE_CERTIFICATE":
      return {
        ...state,
        status: action.response?.statusCode,
        type: action.type,
        err: action.err,
      };
    default:
      return { ...state };
  }
};
export default certificate;
